import React from "react"
import MeOnRock from "../Images/me_on_rock.png"
import GithUB from "../Images/GitHub.png"
import LinkedIn from "../Images/Linkedin.png"
import '../CSS/About.css'

function AboutPage(){
    return(
        <div id="about">
            <h1 >About</h1>
            <p>
                <b>Michael Orlando:</b> a PC enthusiast  that loves to solve 
                problems with code and has an aptitude for security. Always learning
                new frameworks tools, and security trends.
            </p>
            <h2>Prefered Programing Languages</h2>
            <ul>
                    <li>C++</li>
                    <li>Python</li>
                    <li>Ruby</li>
                    <li>javascript,HTML,CSS</li>
            </ul>
            <div>
            <a href="https://github.com/michaeljohnorlando">
                <img className="Icon" src={GithUB} />
            </a>
            <a href="https://www.linkedin.com/in/michael-orlando-998649118/">
                <img className="Icon" src={LinkedIn} />
            </a>
            </div>

            <img id="MeOnRock" src={MeOnRock} />
        </div>
    )
}

export default AboutPage

